.mainContainer {
  background: linear-gradient(#09203F, #537895);
  height: auto;
  min-height: 80vh;
}

.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 2rem;
  width: 65%;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #011936FF;
  border-radius: 50px;
  box-shadow: #09203F 0px 0px 10px 5px;
  border: #09203F 1px solid;
}

.h2 {
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 3rem;
  margin-bottom: 0rem;
  color: #F1F3FFFF;
  font-family: "Baloo 2", sans-serif;
}

.ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  width: 100%;
}

.li {
  margin: 0.5rem;
}

.button {
  background-color: #09203F;
  border: 5px solid #0956a9;
  color: white;
  padding: 1rem 2rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 25px;
  box-shadow: 0px 0px 10px 5px;
  width: 20rem;
}

.button:hover {
  background-color: #03142a;
}

.backButton {
  border-radius: 100%;
  width: 4rem;
  height: 4rem;
  background-color: #09203F;
  border: 5px solid #0956a9;
  color: white;
  position: fixed;
  bottom: 50px;
  left: 50px;
  cursor: pointer;
}

.backButton:hover {
  box-shadow: 0px 0px 10px 5px;
}

.blinkingText {
  color: red;
  text-align: center;
  margin-bottom: -.5rem;
  font-size: 1.2rem;
  font-weight: 700;
  animation: blink 2s infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}